import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "UserList",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      formLabelWidth: "120px",
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      platforms: [
        { value: 1, label: "全部" },
        { value: 2, label: "审核中" },
        { value: 3, label: "已拒绝" },
        { value: 4, label: "已关闭" },
        { value: 5, label: "已完成" },
        { value: 6, label: "申请中" }
      ],
      operas: [
        { value: 1, label: "渠道1" },
        { value: 2, label: "渠道2" }
      ],
      options: [
        { value: "1", label: "启用" },
        { value: "2", label: "在线" },
        { value: "3", label: "离线" },
        { value: "4", label: "冻结" }
      ],
      Vipoptions: [
        { value: "1", label: "全部" },
        { value: "2", label: "vip1" },
        { value: "3", label: "VIP2" },
        { value: "4", label: "vip3" }
      ],
      format: {
        user_id: "",
        phone: "",
        ip: "",
        robot_code: "",
        channel: "",
        order_status: "",
        vip_class: "",
        time_range: ""
      },
      tableStyle: [
        { label: "玩家id", prop: "order_id", width: "" },
        { label: "玩家昵称", prop: "channel_name", width: "" },
        { label: "账号类型", prop: "channel_name", width: "" },
        { label: "VIP等级", prop: "fun_1", width: "" },
        { label: "累计充值", prop: "fun_2", width: "" },
        { label: "手机号码", prop: "fun_3", width: "" },
        { label: "渠道", prop: "fun_4", width: "" },
        { label: "注册时间", prop: "fun_5", width: "" },
        { label: "创建ip", prop: "fun_6", width: "" },
        { label: "最后登录时间", prop: "fun_7", width: "" },
        { label: "最后充值时间", prop: "fun_8", width: "" },
        { label: "最后登录ip", prop: "fun_8", width: "" },
        { label: "是否在线", prop: "fun_8", width: "" },
        { label: "银行卡", prop: "fun_8", width: "" },
        { label: "操作", prop: "action", width: "120" }
      ],
      records: [
        {
          order_id: "10012",
          channel_name: "主包",
          fun_1: "备份",
          fun_2: "排行榜",
          fun_3: "邮箱",
          fun_4: "客服",
          fun_5: "未设定",
          fun_6: "未设定",
          fun_7: "未设定",
          fun_8: "设定",
          operator: "json",
          create_time: "2020-02-10 12:00:00",
          action: ""
        }
      ],
      pageInfo: new PageInfo(0, [5, 10, 15], 5),
      dialogFormVisible: false,
      form: {
        user_id: "",
        pay_type: "1",
        goods_type: 100,
        money: "",
        check_money: ""
      }
    };
  },
  methods: {
    /**搜索*/
    search() {
      let data = this.format,
        user_id = 1000;
      this.userList(data, user_id);
    },
    handelClick(btn, row) {
      if (btn.type === "edit") {
        this.dialogFormVisible = true;
      }
    }
  },
  mounted() {}
};



